import React, { useEffect, useState } from 'react'
import { useParams } from 'react-router-dom';
import { Box } from '@mui/material';
import { fetchData, exercisesOptions,youtubeOptions } from './utils/fetchData';
import Detail from './Detail'
import ExerciseVideos from './ExerciseVideos';
import SimilarExercises from './SilmiliarExercises';
const ExerciseDetail = () => {
  const [exerciseDetail, setExerciseDetail] = useState({});
  const [exerciseVideos, setExerciseVideos] = useState([]);
  const { id } = useParams();
  useEffect(() => {
    const fetchExercisesData = async () => {
      const exerciseDbUrl = 'https://exercisedb.p.rapidapi.com';
      const youtubeSearchUrl = 'https://youtube-search-and-download.p.rapidapi.com';
      const exerciseDetailData = await fetchData(`${exerciseDbUrl}/exercises/exercise/${id}`, exercisesOptions);
      setExerciseDetail(exerciseDetailData);
      const exerciseVideoData = await fetchData(`${youtubeSearchUrl}/search?q=/${setExerciseDetail.name}`, youtubeOptions);
      setExerciseVideos(exerciseVideoData);
    }
    fetchExercisesData();
  },[id])
  return (
    <Box>
      <Detail exerciseDetail={exerciseDetail} />
      <ExerciseVideos exerciseVideos={exerciseVideos} />
      <SimilarExercises/>
    </Box>
  )
}
export default ExerciseDetail